import { useContext, useState } from "react";
import { Link } from "react-router-dom";
import  "./Navbar.css";
import{Icon} from 'react-icons-kit'
import {menu} from 'react-icons-kit/feather/menu'
import {x} from 'react-icons-kit/feather/x'



function Navbar() {

  const [toggle,setToggle]=useState(false)
const handleToggle=()=>{
setToggle(!toggle)
}

  return (
   
    <nav className="navbar">
      <Link to="/" className="logo"><h2 >Rekha</h2></Link>
      <ul className={toggle?"link_mobile":"links"} onClick={()=>setToggle(false)}>
     <Link to="/"className="about"><li>ABOUT</li></Link>
     <Link to="/projects"className="project"><li>PROJECT</li></Link>
     <Link to="/skills"className="skill"><li>SKILLS</li></Link>
     <Link to="/contact"className="contact"><li>CONTACT</li></Link>
      </ul>
    <button className="mobile_icon" onClick={handleToggle}>{toggle ?<Icon icon={x} size={20}/>:<Icon icon={menu} size={20}/>}</button>
      </nav>
  );
}
export default Navbar;

